.main-header {
    background-color: rgb(255, 251, 251);
    // justify-content: space-between;
    position: fixed;
    z-index: 8;
    top: 0;
    width: 100%;
    padding: 4px 15px 0 15px;
    font-size: 18px;

    // grid-column: 1/-1;
}
.header-logo {
    font-size: rem(28px);
    font-family: "GochiHand-Regular";
    // margin-inline-start: 25px;
    // margin-inline-end: 25px;
}

.header-list {
    .avatar {
        width: 30px;
        height: 30px;
        padding: 0;
        margin: -3px;
    }
    .header-link {
        height: 100%;
        margin-inline-end: 25px;
        padding-top: 5px;
        :hover {
            background-color: blanchedalmond;
            border-radius: 15px;

            color: rgb(22, 17, 10);
        }
    }
}

.logo {
    font-family: Airbnb Cereal App Bold;
}
