.filter {
    transform: translate(50%);
    padding-bottom: 10px;
    // background-color: $clr2;
    margin-bottom: 20px;
    // border-radius: 15px;
    width: 50%;
    // position: sticky;
    z-index: 10;
    position: sticky;
    top: calc(0.2em);
    // outline: 1px solid $clr2;
    // -moz-outline-radius: 15px;
    // box-shadow: 1px 4px 8px 0px rgba(0, 0, 0, 0.3);
    .title {
        min-width: 450px;
    }
    input,
    select {
        // border-bottom: 1px solid black;
        border-top: none;
        border-right: none;
        margin-bottom: 0px;

        border-left: none;
        margin: 0;
        -webkit-appearance: none;
        -moz-appearance: none;
        text-indent: 1px;
        text-overflow: "";
        &:hover {
            border-bottom: 1px solid black;
        }

        // background-color: #fbfbfb;
    }
    label {
        margin-left: 15px;
        // margin: 0px;
        margin-top: 10px;
        padding-bottom: 0px;
    }
    option {
        position: absolute;
        z-index: 1;
        opacity: 0;
        width: 10px;
    }
}

.searchIcons {
    // background-color: rgb(12, 11, 11);
    // border-end-end-radius: 50%;
    color: #222121;
    border-radius: 50%;
    padding: 5px;
    margin-top: 5px;
    margin-left: -30px;
    z-index: 8;
    // position: relative;
    // right: 10px;
    // cursor: pointer;
    // box-shadow: 1px 4px 8px 1px rgba(0, 0, 0, 0.3);
}
select::-ms-expand {
    display: none;
}
